  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 10px;
}

/* Стилизация полосы прокрутки (только для браузеров на базе WebKit, таких как Chrome, Edge, Safari) */
.status_els::-webkit-scrollbar {
  width: 8px;
  /* Ширина вертикальной полосы прокрутки */
}

/* Цвет дорожки (фон полосы прокрутки) */
.status_els::-webkit-scrollbar-track {
  background: #f0f0f0;
  /* Светлый фон */
  border-radius: 8px;
}

/* Цвет самой полосы (ползунка) */
.status_els::-webkit-scrollbar-thumb {
  background: #888;
  /* Серый цвет */
  border-radius: 8px;
}

/* Ховер-эффект на ползунке */
.status_els::-webkit-scrollbar-thumb:hover {
  background: #555;
  /* Темнее при наведении */
}


