  width: 700px;
  padding: 35px;
  background-color: #eae3e3;
  margin: 0 auto;
  margin-top: 16px;

  /* Центрирование блока */
  top: 200px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.top-filter-block{
  width: 100%;
