/* //commit2 */
input,
select {

  padding: 7px;
  margin: 0 auto;
  border: 1px solid #999;
  border-radius: 3px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease-in-out;
}

input:focus {
  outline: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.input_td div {

  font-size: 14px;
}

.input_td input,
select {
  margin-top: 15px;
}

