/* //commit2 */
.mainmenubtn {
  background-color: #2A5B4A;
  color: white;
  border: none;
  cursor: pointer;
  padding: 7px;
  margin-top: 0px;
  width: 170px;
  text-align: center;
}

.mainmenubtn:hover {
  background-color: red;
}

.dropdown {
  padding: 0;
  position: relative;
  display: inline-block;
}

.dropdown-child {
  display: none;
  background-color: rgb(247, 247, 247);
  min-width: 200px;
}

.dropdown-child a {
  color: white;
  padding: 20px;
  text-decoration: none;
  display: block;
}

.dropdown:hover .dropdown-child {
  display: block;
}



@media (min-height:851px) and (max-height:1000px) and (max-width: 700px) {
  .left_menu {
    order: 2;
    width: 100%;
    margin-bottom: 60px;
    height: max-content;
    margin-top: auto;
  }
}

@media (max-height:850px) and (max-width: 700px) {
  .left_menu {
    order: 2;
    width: 100%;
    margin-bottom: 30px;
    height: max-content;
    margin-top: auto;
  }
}

@media (max-width:700px) {
  .top-filter-block, .bottom-filter-block{
    display: block;
  }

  .fixed_search_form h2{
    display: none;
  }

  .search_form .search{
    margin-left: 0;
  }

  .sort-el-block input, .sort-el-block select{
    width: 100%;
    padding-left: 0;
  }

  .sort-el-block{
    width: 100%;
    margin-top: 10px;
  }

  .update-block {
    display: none;
  }

  .search {
    margin-left: 15px;
  }

  input,
  textarea,
  select {
    font-size: 16px;
  }

  .login_form {
    width: 80%;
  }

  .main {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;

  }



  .right_content {
    order: 1;
  }

  .menu_block {
    display: flex;
    justify-content: space-between;
  }

  .mob {
    display: none;
  }

  .statusBlock {
    max-height: 80vh;
    margin-top: 0px;
  }

  .fixed_search_form {
    position: absolute;
    width: 100%;
  }

